// Components
import BaseHeader from '@/components/argon-core/BaseHeader'
import MainTitle from '@/components/shack/MainTitle.vue'
import SectionTitle from '@/components/shack/SectionTitle.vue'
import UploadButton from '@/components/shack/UploadButton.vue'
import { Select, Option } from 'element-ui'

import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  components: {
    BaseHeader,
    MainTitle,
    UploadButton,
    SectionTitle,
    [Select.name]: Select,
    [Option.name]: Option
  },
  async asyncData({ store, $eatry, error }) {
    await $eatry
      .getAllEatryCategories()
      .then(data => {
        store.commit('eatry/setCategories', data)
      })
      .catch(err => {
        error({
          statusCode: err.status || 404,
          message: err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message
        })
      })
  },
  data() {
    return {
      loading: false,
      name: '',
      description: '',
      capacity: 10,
      price: 10,
      eatry_category_id: null,
      can_order: '',
      banner_url: '',
      is_special: '',
      breakfast: {
        from: '06:00',
        to: '11:00',
        weekdays: [1, 6]
      },
      lunch: {
        from: '12:00',
        to: '14:00',
        weekdays: [1, 6]
      },
      dinner: {
        from: '15:00',
        to: '00:00',
        weekdays: [1, 6]
      },
      all: {
        from: '00:00',
        to: '00:00',
        weekdays: [1, 6]
      },
      eatry_availability: []
    }
  },
  computed: {
    ...mapState({
      categories: state => state.eatry.categories.data
    })
  },
  methods: {
    async addInventory() {
      const {
        name,
        description,
        capacity,
        price,
        banner_url,
        eatry_category_id,
        eatry_availability,
        can_order,
        is_special
      } = this

      this.loading = !this.loading

      const payload = {
        name,
        description,
        capacity,
        price,
        banner_url,
        eatry_category_id,
        eatry_availability,
        can_order: Boolean(can_order),
        is_special: Boolean(is_special)
      }

      this.$eatry
        .addOneEatry(payload)
        .then(({ data }) => {
          this.$bvToast.toast(`${name} add successfully`, {
            title: 'Success',
            variant: 'success',
            solid: true
          })

          setTimeout(() => {
            this.$router.push({ name: 'space-eatry-inventory' })
          }, 3000)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message
          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })

          this.loading = !this.loading
        })
    }
  }
}
